  align-items: center;
}

.app__bg {
  background: url(/perlemoen-restaurant/static/media/bg.d719065b.png);
  background-position: center;
  background-size: cover;
  background-repeat: repeat;
  background-attachment: fixed;
}

.app__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  min-height: 100vh;
}

.app__wrapper_info {
