.app__aboutus {
    position: relative;
    background: var(--color-black);
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 1050px;
    height: 1740px;
    z-index: 0;
    margin: 0rem 37rem 0rem 60rem;
}


.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: right;
}

.app__aboutus-content_knife {
    margin: 0.25rem 0.25rem;
    width: 50%;
    height: 50%;
}


.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    margin: 2rem 0;
    color: var(--color-grey);
    text-transform: none;
}


.app__aboutus-link a:hover {
    color: var(--color-white);
}

    .app__aboutus-content_history p,
    .app__aboutus-content_about p {
        font-family: var(--font-alt);
    }

    @media screen and (max-width: 2000px) {

        .app__aboutus-content_knife img {
            height: 65rem;
            margin: 4rem 4rem 2rem 7rem;
            
        }
    }

@media screen and (max-width: 950px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_about {
        flex: 1;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        text-align: left;
    }


    .app__aboutus-content_knife {
        margin: 2rem 3rem 2rem 2rem;
    }
}